.features {
  padding: 20rem 0;
  background-image: linear-gradient(to right bottom, rgba($color-lgreen, .8), rgba($color-dgreen, 0.8)), url(../img/nat-4.jpg);
  background-size: cover;
  transform: skewY(-7deg);
  margin-top: -12rem;

  @include respond(tab-port) {
    padding: 10rem 0;
  }

  & > * {
    transform: skewY(7deg);
  }
}